import { writable } from 'svelte/store';
async function loadChaptersMetadata() {
    const res = await fetch('./chapters.json');
    return await res.json();
}
function createChapterStore() {
    const { subscribe, set, update } = writable({
        metadata: [],
        selectedIndex: null,
    });
    const setup = () => loadChaptersMetadata()
        .then((chaptersMetadata) => {
        set({
            metadata: chaptersMetadata,
            selectedIndex: 0
        });
    })
        .catch(console.error);
    setup();
    return {
        subscribe,
        selected: (chapterIndex) => update((store) => {
            store.selectedIndex = chapterIndex;
            return store;
        }),
        reset: setup
    };
}
const chapterStore = createChapterStore();
export default chapterStore;
